.istorijat {
  padding: 100px 0 50px;
  background: #f8fafc;
  min-height: 100vh;
}

.istorijat h1 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #1f2937;
}

/* Timeline */
.timeline {
  max-width: 800px;
  margin: 0 auto 4rem;
  position: relative;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #2563eb;
  transform: translateX(-50%);
}

.timeline-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  position: relative;
}

.timeline-item:nth-child(odd) {
  flex-direction: row-reverse;
}

.timeline-date {
  flex: 1;
  text-align: center;
  font-weight: bold;
  color: #2563eb;
  font-size: 1.1rem;
}

.timeline-content {
  flex: 1;
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  margin: 0 2rem;
  position: relative;
}

.timeline-content::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 20px;
  height: 20px;
  background: #2563eb;
  border-radius: 50%;
  transform: translateY(-50%);
}

.timeline-item:nth-child(even) .timeline-content::before {
  right: -30px;
}

.timeline-item:nth-child(odd) .timeline-content::before {
  left: -30px;
}

/* Motivacija */
.motivacija {
  max-width: 800px;
  margin: 0 auto;
}

.motivacija h2 {
  text-align: center;
  margin-bottom: 2rem;
  color: #1f2937;
}

.motivacija-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.motivacija-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.motivacija-card h3 {
  color: #2563eb;
  margin-bottom: 1rem;
}

/* Responsive */
@media (max-width: 768px) {
  .timeline::before {
    left: 30px;
  }
  
  .timeline-item {
    flex-direction: row !important;
    align-items: flex-start;
  }
  
  .timeline-date {
    flex: 0 0 80px;
    text-align: left;
  }
  
  .timeline-content {
    margin-left: 3rem;
    margin-right: 0;
  }
  
  .timeline-content::before {
    left: -40px !important;
    right: auto !important;
  }
}